@media only screen and (min-width: 962px) {
    .Single {
        display: flex;
        flex-flow: column wrap;
        align-items: center;
        /* justify-content: center; */
        margin-top: 100px;
        font-size: 16px;
        width: 100%;
        min-height: 100vh;
        max-height: 100%;
        color: var(--primary-color);
        background-color: var(--secondary-color);
    }

    .Blog-Header {
        width: 80%;
        border-bottom: 1px solid #808080;
        align-items: center;
        display: flex;
        flex-flow: column wrap;
        margin: 50px 0px;

        text-transform: capitalize;
    }

    .Blog-Header a {
        text-decoration: none;
        color: var(--primary-color);
    }

    .BlogLinkers {
        margin-bottom: 50px;
    }

    .AuthorSingle {
        display: flex;
        flex-flow: row wrap;
        justify-content: center;
        align-items: center;
        margin: 20px 0px;
        text-transform: capitalize;
    }

    .authorItemSingle {
        margin: 0px 10px;
        object-fit: cover;

    }

    .authorItemSingle img {
        width: 100px;
        height: 100px;
        border-radius: 50px;
        object-fit: initial;
    }

    .SingleBlog {
        display: flex;
        flex-flow: column wrap;
        margin-top: 10px;
        width: 80%;
        justify-content: center;


    }

    .SingleBlogTitle {
        font-size: 4rem;
        padding: 5px 0px;
        text-transform: capitalize;
        text-align: center;
    }

    .BlogImage {
        width: 100%;
        height: 600px;
        margin-bottom: 40px;
        object-fit: cover;
    }

    .BlogImage img {
        width: 100%;
        height: 100%;
        object-fit: unset;
    }

    .BlogDescription {
        width: 100%;
        margin-bottom: 30px;
        text-align: justify;
    }
}

